@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);




body {
  font-family: 'Share Tech Mono',
    monospace;
  font-weight: 100;
  margin: 0;
}

h1,
h2,
h3 {
  font-family: "VT323", sans-serif;
}

h1 {
  color: #1292B9;
}

p {
  font-family: 'Share Tech Mono',
    monospace;
  font-weight: 400;
}
.video-container {
  margin-top: -300px;
  width: 100%;
  z-index: -999;
  position: absolute;
  opacity: 40%;
}

.background-video {
  width: 100vw;
}

.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px;
}

button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
}

.my-button {
  width: 197px;
  background-color: #CB295F;
  color: #FFFFFF;
  font-family: "VT323",
    sans-serif;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 18px;
  text-align: center;
  padding: 10px 30px;
  display: block;
}

.my-button:hover {
  width: 197px;
  background-color: #F7CA18;
  color: black;
  font-family: "VT323",
    sans-serif;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 18px;
  text-align: center;
  padding: 10px 30px;
  display: block;
}

.my-mini-button {
  color: #1292B9;
  font-family: "VT323",
    sans-serif;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 18px;
  padding: 10px 30px;
  display: block;
  margin: 0 auto;
}

.my-mini-button:hover {
  color: #0D7282;
  font-family: "VT323",
    sans-serif;
  font-size: 24px;
  letter-spacing: 1.5px;
  line-height: 18px;
  padding: 10px 30px;
  display: block;
  margin: 0 auto;
}

.my-button-blue {
  background-color: #1292B9;
  width: 197px;
  color: #FFFFFF;
  font-family: "VT323",
    sans-serif;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 18px;
  text-align: center;
  padding: 10px 30px;
  display: block;
}

.my-button-blue:hover {
  background-color: #0D7282;
