.homepage {
  background-image: url("/img/bkg.svg");
  background-position: 0 57px;
  padding: 40px 0px 150px;
  background-repeat: no-repeat;
}

.homepage h1 {
  color: #1292b9;
  font-size: 60px;
  letter-spacing: 5px;
  line-height: 60px;
  margin-bottom: 24px;
}

.homepage h3 {
  font-family: "Share Tech Mono", monospace;
  color: black;
  font-size: 40px;
  letter-spacing: 3px;
  line-height: 42px;
  margin-bottom: 40px;
}


.homepage .homeHeader {
  padding: 200px 0 0;
