  text-align: center;
}

.home-card p {
  color: #867f79;
  font-family: "Share Tech Mono", monospace;
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 21px;
  text-align: center;
}

.every-game {
  margin-top: 214px;
}

